.App {
  text-align: center;
  max-width: 1000px;
  height: 900px;
  margin: auto;
  padding-top: 50px;
}

.green-text{
  color: #91C11E;
}

/* Appbar */

.bg{
  background-color: #F4B74E;
}

.appbar{
  max-width: 1000px;
  height: 90px;
  margin: auto;
  display: flex;
  background-color: #FFFFFF;
}


.appbar .item{
  width: 100%;
  text-align: left;
  display: flex;
  gap: 10px;
  align-items: center;
}

.item p{
  margin: 0;
}


.item .name{
  font-size: 16px;
  font-weight: bold;
}

.item .text{
  color: #9B9B9B;
  font-size: 11px;
}

/* Form */

.form{
  width: 765px;
  background-color: #FFFFFF;
  margin: auto;
  box-shadow: 0 10px 20px 0 rgb(0 0 0 / 16%);
  border-radius: 10px;
}
.header h2{
  width: 561px;
  color: #AFAFAF;
  font-family: Montserrat;
  font-size: 37.5px;
  font-weight: 800;
  line-height: 46px;
  text-align: center;
  left: 0;
  right: 0;
  margin: auto;
  padding: 60px 0;
}

.wrap-form{
  height: 400px;
  background-color: #F7F7F7;
}

form{
  width: 570px;
  margin: auto;
}

.styled-select{
  display: flex;
  flex-direction: column;
}

label{
  color: #555555;
  font-family: "Source Sans Pro";
  font-size: 18.8px;
  line-height: 29px;
}

select{
  padding: 10px 70px 10px 13px ;
  margin-top: 10px;
  border: none;
  border-right: 10px solid #FFFFFF;
  height: 51px;
  width: 567px;
  border-radius: 3px;
  background-color: #FFFFFF;
}

.input-button {
  margin-top: 40px;
  text-align: center;
}

.button {
  color: #FFFFFF;
  border: none;
  font-family: "Source Sans Pro";
  font-size: 15.4px;
  font-weight: bold;
  line-height: 19.6px;
  height: 51px;
  width: 222px;
  border-radius: 5px;
  background-color: #91C11E;
}